import React from "react";
import { useHistory } from "react-router-dom";
import { Navbar as BPNavbar, NavbarGroup, NavbarHeading, NavbarDivider, Button } from "@blueprintjs/core";

import { SessionStore } from "modules/auth";

export const Navbar: React.FC = () => {
    console.log("Navbar!!");
    const history = useHistory();

    return (
        <BPNavbar className="bp3-dark" fixedToTop={true}>
            <NavbarGroup align="left">
                <NavbarHeading>Super App</NavbarHeading>
            </NavbarGroup>

            <NavbarGroup align="right">
                <Button
                    icon="home"
                    minimal
                    onClick={() => {
                        history.push("/");
                    }}
                ></Button>
                
                <NavbarDivider />

                <Button
                    icon="chat"
                    minimal
                    onClick={() => {
                        history.push("/dialog-viewer");
                    }}
                ></Button>

                <NavbarDivider />

                <Button
                    icon="user"
                    minimal
                    onClick={() => {
                        history.push("/profile");
                    }}
                ></Button>

                <NavbarDivider />

                <Button
                    rightIcon="power"
                    minimal
                    onClick={() => {
                        SessionStore.logout();
                    }}
                >
                    Logout
                </Button>
            </NavbarGroup>
        </BPNavbar>
    );
};

export default Navbar;
