import React from "react";
import { Route } from "react-router-dom";

import LoginPage from "./components/LoginPage/LoginPage";
import AccessControl, { AccessLevel } from "./components/AccessControl/AccessControl";

export { AccessControl, AccessLevel };
export { default as SessionStore } from "./stores/sessionStore";

export const routes = [
    <Route key="login-page" path="/login">
        <AccessControl accessLevel={AccessLevel.NotLoggedIn} redirectTo="/">
            <LoginPage />
        </AccessControl>
    </Route>
];