import React from "react";
import { Redirect } from "react-router-dom";
import { observer } from "mobx-react";
import { Spinner } from "@blueprintjs/core";

import { SessionStore } from "../..";

export enum AccessLevel {
    NotLoggedIn,
    LoggedIn
}

type AccessControlProps = {
    redirectTo?: string | undefined;
    accessLevel?: AccessLevel | undefined;
    children?: React.ReactNode[] | React.ReactNode;
};

export const AccessControl: React.FC<AccessControlProps> = observer(
    ({ redirectTo = "/", accessLevel, children }) => {
        if (SessionStore.isLoading) {
            return <Spinner />;
        }

        if (accessLevel === AccessLevel.NotLoggedIn && SessionStore.isLoggedIn) {
            console.log(`is logged, trying to go to NotLoggedIn, redirect`);
            return <Redirect to={redirectTo} />;
        }

        if (accessLevel === AccessLevel.LoggedIn && !SessionStore.isLoggedIn) {
            console.log(`not logged, trying to go to LoggedIn, keep target and redirect to login`);
            return <Redirect to={`${redirectTo}?r=${window.location.pathname}`} />;
        }

        console.log("render children");
        return <>{children}</>;
    }
);

export default AccessControl;
