import { observable, action, computed } from "mobx";
import { sleep } from "modules/shared/utils";

class SessionStore {
    @observable isLoading: boolean = true;
    @observable session: {} | null = null;

    @action setSession(session: {} | null) {
        console.log("setting session to ", session);
        this.session = session;
    }

    @action async restoreSession() {
        this.isLoading = true;

        await sleep(200);

        this.session = { username: "Bob" };
        this.isLoading = false;
    }

    @action async login(session: {}) {
        console.log("logging in");
        this.isLoading = true;

        await sleep(200);

        this.setSession(session);
        this.isLoading = false;
    }

    @action logout() {
        console.log("logging out");
        this.session = null;
    }

    @computed get isLoggedIn() {
        return this.session !== null;
    }
}

export default new SessionStore();
