import React from "react";
import { InputGroup, Card, Elevation, Button } from "@blueprintjs/core";
import { Grid, Row, Col } from "react-flexbox-grid";

import { PageTitle } from "modules/shared";
import { SessionStore } from "modules/auth";

const LoginPage: React.FC = () => {
    console.log("login page!");

    const onLogin = async () => {
        await SessionStore.login({ username: "foo" });
        console.log("cool");
    };

    return (
        <>
            <PageTitle title="Login" />
            <Grid>
                <Col xs={12}>
                    <Row center="xs">
                        <Col xs={6}>
                            <Card elevation={Elevation.THREE}>
                                <Row style={{ marginTop: 10, marginBottom: 10 }}>
                                    <InputGroup fill placeholder="Username" large />
                                </Row>

                                <Row style={{ marginTop: 10, marginBottom: 10 }}>
                                    <InputGroup fill placeholder="Password" large />
                                </Row>

                                <Row end="xs">
                                    <Button onClick={onLogin} large>
                                        Login
                                    </Button>
                                </Row>
                            </Card>
                        </Col>
                    </Row>
                </Col>
            </Grid>
        </>
    );
};

export default LoginPage;
