import React from "react";
import { Route } from "react-router-dom";

import { AccessControl, AccessLevel } from "modules/auth";
import DialogViewerPage from "./components/DialogViewerPage/DialogViewerPage";

const routes = [
    <Route key="dialogs-viewer-page" exact path="/dialog-viewer">
        <AccessControl accessLevel={AccessLevel.LoggedIn} redirectTo="/login">
            <DialogViewerPage />
        </AccessControl>
    </Route>
];

export { routes };
