import React from "react";
import { BrowserRouter as Router, Redirect, Switch, Route } from "react-router-dom";
import "normalize.css/normalize.css";
import "@blueprintjs/icons/lib/css/blueprint-icons.css";
import "@blueprintjs/core/lib/css/blueprint.css";

import { routes as authRoutes, SessionStore } from "modules/auth";
import { routes as homeRoutes } from "modules/home";
import { routes as profileRoutes } from "modules/profile";
import { routes as dialogSystemRoutes } from "modules/dialog-system";

import "./App.scss";

export const App: React.FC = () => {
    React.useEffect(() => {
        SessionStore.restoreSession();
    }, []);

    return (
        <Router>
            <Switch>
                {authRoutes}
                {homeRoutes}
                {profileRoutes}
                {dialogSystemRoutes}
                <Route>
                    <Redirect to="/" />
                </Route>
            </Switch>
        </Router>
    );
};

export default App;
